___CSS_LOADER_EXPORT___.push([module.id, "\n#balekMainScreenshot {\n  max-width: 80%;\n}\n", "",{"version":3,"sources":["webpack://./src/components/balek/Info.vue"],"names":[],"mappings":";AA4IA;EACE,cAAc;AAChB","sourcesContent":["<template>\n  <div class=\"InfoDiv\">\n    <div class=\"MainInfoTitleDiv\">\n      <img class=\"MainInfoTitleLogoImg\" :src=\"balekLogo\" alt=\"Balek Logo\" />\n      Balek\n      <div class=\"MainInfoSummary\">Full Stack Javascript Framework</div>\n    </div>\n\n    <br />\n    <a href=\"/balek/resources/screenshot.jpg\" target=\"_blank\"\n      ><img\n        id=\"balekMainScreenshot\"\n        :src=\"screenShotThumb\"\n        alt=\"Balek Screenshot\"\n    /></a>\n    <div class=\"MainInfoDiv\">\n      <div class=\"MainInfoDiv\">\n        Balek is a full-stack JavaScript framework designed for developing,\n        building, and deploying web applications that are remotely accessible\n        and persist separate from their interfaces. With Balek, you can build\n        modules using the provided APIs then deploy individually or in\n        combination to create powerful web applications.\n        <br />\n        <br />\n        The framework includes login and user management modules to streamline\n        the development process. Balek also provides a modular architecture that\n        makes it easy to add custom functionality as your project evolves.\n        <br />\n        <br />\n        Click the screenshot to see a larger version, and watch our use cases\n        video to see Balek in action.\n      </div>\n\n      <div\n        class=\"MainInfoSubTitleDiv\"\n        style=\"\n          text-align: left;\n          margin-top: 30px;\n          border-bottom: solid black 2px;\n        \"\n      >\n        Use Cases:\n      </div>\n      <div class=\"MainInfoDiv\">\n        Balek has been used to prototype and deploy range of successful\n        projects, including:\n        <ul>\n          <li>\n            Diaplode: A multi-user remote workspace used for staging projects\n            and researching notes.\n          </li>\n          <li>\n            Coopilot: A web and iOS app used by a point-of-sale department at a\n            local food co-op.\n          </li>\n          <li>\n            Balekute: A Login Module for Balek with an iOS/MacOS universal app\n            to log in using on device encryption.\n          </li>\n        </ul>\n\n        These projects have been deployed and tested on various platforms,\n        including:\n        <ul>\n          <li>MacOS</li>\n          <li>Linux</li>\n          <li>OpenBSD</li>\n          <li>Windows</li>\n          <li>Google Cloud</li>\n        </ul>\n        These examples demonstrate Balek's versatility and ability to support a\n        variety of projects and environments.\n        <br />\n        <br />\n        The video below provides a brief overview of some of these projects.\n        <br />\n        <br />\n        <div class=\"MainYouTubeDiv\">\n          <iframe\n            class=\"MainYoutubeVideoIframe\"\n            src=\"https://www.youtube.com/embed/n3bwKBiYDTU\"\n            title=\"YouTube video player\"\n            frameborder=\"0\"\n            allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\"\n            allowfullscreen=\"\"\n          ></iframe>\n        </div>\n      </div>\n      <div\n        class=\"MainInfoSubTitleDiv\"\n        style=\"\n          text-align: left;\n          margin-top: 30px;\n          border-bottom: solid black 2px;\n        \"\n      >\n        Availability:\n      </div>\n      <div class=\"MainInfoDiv\">\n        Balek is in the process of being cleaned up and documented for public\n        use. <br /><br />\n        Once completed, a Pre-Alpha branch will be available\n        <a\n          target=\"_blank\"\n          class=\"MainLink\"\n          href=\"https://www.github.com/digivigil/balek\"\n          >on Digivigil's Github</a\n        >\n\n        <br />\n        <br />\n        The Current prototype version can be found\n        <a\n          target=\"_blank\"\n          class=\"MainLink\"\n          href=\"https://www.github.com/ephedrandrox/balek\"\n          >here</a\n        >\n        <br />\n        <br />\n      </div>\n    </div>\n  </div>\n</template>\n<script>\nimport balekLogo from \"./resources/Balek.svg\";\n\nimport screenShotThumb from \"./resources/screenshotThumb.png\";\n\nexport default {\n  name: \"BalekInfo\",\n  data: function () {\n    return {\n      balekLogo,\n      screenShotThumb,\n    };\n  },\n};\n</script>\n<style>\n#balekMainScreenshot {\n  max-width: 80%;\n}\n</style>\n"],"sourceRoot":""}]);
